import { Component, Prop, State, h, Listen } from "@stencil/core";

@Component({
  tag: "wxcc-guide-panel",
  styleUrl: "wxcc-guide-panel.css",
  shadow: true

})
export class SideDrawer {
  @State() showNav = false;
  @Prop({ reflect: true }) arttitle: string;
  @Prop({ reflect: true, mutable: true }) opened: boolean;
  @State() content
  @State() lessons: { title: string, url: string }[] = []
  @State() currentPage
  @Prop({ reflect: true }) lList: string;

  async componentWillLoad() {

    const response = await fetch(this.lList)
    // sessionStorage.setItem("lessons", await response.text())
    const lessons = await response.json()
    this.lessons = await lessons.map(match => {
      return { title: match["title"], url: match["address"] }

    })

  }


  // onCloseDrawer(){
  //     this.opened=false;
  // }
  onContentChange(content: string) {
    this.showNav = content === "nav"
    // console.log(content)
  }
  // @Method()
  // async open(){
  //   this.opened=true 
  // }

  @Listen('toggleGuide', { target: "body" })
  onToggle(event: CustomEvent) {
    console.log(event.detail)
    this.opened = !this.opened
  }

  @Listen('wxccGuidePageTurn', { target: "body" })
  onNext(event: CustomEvent) {
    // console.log(event.detail)
    let url
    const iHere = this.lessons.findIndex(x => x.title === this.currentPage)
    if (!iHere) {
      this.currentPage = this.lessons[0].title
    }
    // console.log(this.currentPage)
    // console.log(iHere)
    this.currentPage = this.lessons[iHere + 1].title
    if (event.detail === "next") {
      url = this.lessons[iHere + 1].url
      this.currentPage = this.lessons[iHere + 1].title
    } else {
      url = this.lessons[iHere - 1].url
      this.currentPage = this.lessons[iHere - 1].title
    }
    this.getHTML(url)
      .then(html => {
        if (html !== null) {
          this.content = html
          this.content = <div innerHTML={this.content}></div>
          // console.log(html);

        }
      });
      this.showNav = false
  }

  onChoice(url) {
    this.getHTML(url)
      .then(html => {
        if (html !== null) {
          this.content = html
          this.content = <div innerHTML={this.content}></div>
          this.currentPage = this.lessons[this.lessons.findIndex(x => x.url === url)].title
          this.showNav = false
        }
      })
  }

  async getHTML(url) {
    try {
      const response = await fetch(url);

      if (!response.ok) {
        throw new Error(`HTTP error! Status: ${response.status}`);
      }

      const html = await response.text();
      return html;
    } catch (error) {
      console.error("Error during fetch:", error);
      return null;
    }
  }
 componentDidUpdate(){
  
    Array.from(document.querySelector("wxcc-guide-panel").shadowRoot.querySelectorAll("textarea")).forEach((element:any) => {element.value = element.value.replaceAll("\\","")})

 }
  render() {
    let mainContent = this.content || <slot />;
    if (this.showNav) {
      mainContent = <ul class="nav">{this.lessons.map(lesson => (
        <li class="nav" onClick={this.onChoice.bind(this, lesson.url)}><strong>{lesson.title}</strong></li>
      ))}</ul>
    }


    return [
      // <div class="backdrop" onClick={this.onCloseDrawer.bind(this)}/>,
      <aside >
        <header>
          <h1 class="post-title">{this.currentPage || this.arttitle}</h1>
          {/* <button onClick={this.onCloseDrawer.bind(this)}>X</button> */}
        </header>

        <section id="tabs">
          <button class={!this.showNav ? "active" : ""} onClick={this.onContentChange.bind(this, "lesson")}>Lesson</button>
          <button class={this.showNav ? "active" : ""} onClick={this.onContentChange.bind(this, "nav")}>Navigation</button>
        </section>
        <main>
          {mainContent}
        </main>
      </aside >
    ];
  }
}
